<template>
  <div
    class="flex items-center gap-spacing-100 whitespace-nowrap rounded-04 bg-bg-neutral-raised px-spacing-100 py-spacing-50"
  >
    <p
      class="text-body/s/relaxed/400"
      :class="
        isDisabled
          ? 'text-content-on-neutral-tertiary'
          : 'text-content-on-neutral-primary'
      "
    >
      {{ itemName }}
    </p>
    <div @click="handleRemove()" class="cursor-pointer">
      <IconX
        class="h-spacing-150 w-spacing-150 hover:text-constant-status-error"
        :class="
          isDisabled
            ? 'text-content-on-neutral-tertiary'
            : 'text-content-on-neutral-secondary'
        "
      />
    </div>
  </div>
</template>

<script>
export default {
  components: {
    IconX: () => import("@/components/svgIcons/x.vue"),
  },
  props: {
    itemName: {
      type: String,
      required: true,
    },
    isDisabled: {
      type: Boolean,
      required: false,
      default: false,
    },
  },
  methods: {
    handleRemove() {
      this.$emit("on-item-removed");
    },
  },
};
</script>
